export default {
	data() {
		return {
			id:'',
			imageUrl:'',
			show: false,
			showIn: false
		}
	}, 
	onLoad(params) {
		if(params && params.id){
			this.id = params.id;
		}
		this.imageUrl = '/static/swiper01.jpg'
		
	},
	methods: {
		test(){
			uni.chooseImage({
				count: 1,
				sourceType: ['camera'],
				success: function (res) {
					uni.saveImageToPhotosAlbum({
						filePath: res.tempFilePaths[0],
						success: function () {
							console.log('save success');
						} 
					});
				}
			})
		}
		,
		 submit() {
			 //todo :接口联调
			 if(1===1) {
				this.show = true;
				this.showIn = true;
			 }else{
				this.show = true;
				this.showIn = false;
			 }
		 },
		  cancel(v) {

			this.show = false
		 	if (v === '0') {
		 		uni.switchTab({
		 			url: "/pages/home/home"
		 		})
		 	}		 
		 },
		 toContract() {
			 //跳转到 我的-合同
			 uni.navigateTo({
			 	url: ""
			 }) 
		 }
	}
}
